
.Node {
  border: solid;
  border-color: gray;
  border-width: 1px;
  background-color: white;
}
.Node:hover {
  cursor: pointer;
}

.Tree {
  position: relative;
  display: flex;
  justify-content: space-evenly;
  flex-direction: column;
  height: 400px;
}

@-webkit-keyframes activate {
  from {
    border-color: gray;
    stroke: gray;
  }
  to {
    border-color: gold;
    stroke: gold;
  }
}

@keyframes activate {
  from {
    border-color: gray;
    stroke: gray;
  }
  to {
    border-color: gold;
    stroke: gold;
  }
}
.Tree .active {
  -webkit-animation: 1.5s forwards activate;
          animation: 1.5s forwards activate;
}

.Tree-Links {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
}
.Tree-Links > path {
  fill: none;
  stroke: gray;
  stroke-width: 6px;
}

.Tree-Row {
  display: flex;
  justify-content: space-evenly;
  height: 64px;
}
